import { GET_ERRORS, SET_CURRENT_USER, SET_CURRENT_ORDER } from "./types";
import axios from "axios";
import setAuthToken from "../util/setAuthToken";
import jwt_decode from "jwt-decode";
// Register User
export const registerUser = (userData, history) => dispatch => {
  axios
    .post("/api/users/register", userData)
    .then(res => history.push("/login"))
    .catch(err =>
      dispatch({
        type: GET_ERRORS,
        payload: err.response.data
      })
    );
};

export const editProfile = (formData, history) => dispatch => {
  axios
    .put("/api/users/edit/profile", formData)
    .then(res => history.push("/profile"))
    .catch(err =>
      dispatch({
        type: GET_ERRORS,
        payload: err.response.data
      })
    );
};

//login - Get User token
export const loginUser = userData => dispatch => {
  axios
    .post("/api/users/login", userData)
    .then(res => {
      const { token } = res.data;
      localStorage.setItem("jwtToken", token);
      //set token to Auth header
      setAuthToken(token);
      // Decode token to get user data
      const decode = jwt_decode(token);
      //Set current user
      dispatch(setCurrentUser(decode));
    })
    .catch(err => {
      dispatch({
        type: GET_ERRORS,
        payload: err.response.data
      });
    });
};
//Order
export const currentOrder = () => dispatch => {
  axios
    .get("/api/orders/current")
    .then(res => {
      const order = res.data;
      var sum_amt = 0;
      var thisorder = {
        amount : 0,
        order : res.data
      }
      for (let data in order) {
        if (
          order.hasOwnProperty(data) &&
          typeof order[data] === "object"
        ) {
          for (let i = 0; i < order[data].length; i++) {
            sum_amt += order[data][i].amount;
          }
        }
      }
      thisorder.amount = sum_amt
      //Set current user
      dispatch(setCurrentAmountOrder(thisorder));
    })
    .catch(err => {
      console.log(err)
      dispatch({
        type: GET_ERRORS,
        payload: err
      });
    })
};

export const setCurrentAmountOrder = amount_order => {
  return {
    type: SET_CURRENT_ORDER,
    payload: amount_order
  };
};

export const setCurrentUser = decode => {
  return {
    type: SET_CURRENT_USER,
    payload: decode
  };
};

//log user out
export const logoutUser = () => dispatch => {
  // Remove token from localStorage
  localStorage.removeItem("jwtToken");
  // Remove auth header for future requests
  setAuthToken(false);
  //Set current user to {} which will set isAutherticated to false
  dispatch(setCurrentUser({}));
};
